import { NavBar, Icon,Grid,Tabs, Flex, WhiteSpace   } from 'antd-mobile';
import React, { Component } from 'react'
import Banner from './shopbanner';
import './shop.css'


const data = Array.from(new Array(10)).map((_val, i) => ({
    icon: './images/chuang.png',
    text: `床`,
  }));

export default class shop extends Component {
    
    render() {              
        return (
            <div>
                 <NavBar
                
                style={{background:'#33cccc',color:'#fffb'}}

                onLeftClick={() => console.log('onLeftClick')}
                rightContent={[
                    <Icon key="0" type="search" style={{ marginRight: '16px' }} />,
                    // <Icon key="1" type="ellipsis" />,
                ]}
                >
                    商城
                </NavBar>
                <Banner />
                <Grid data={data} hasLine={false} columnNum={5} />
                
                    <div style={{ height: 'px' }}>
                        <div className="flex-container">
                        <WhiteSpace size="lg" />
                        <Flex>
                            <Flex.Item><img src='./images/01.png' />Top Art Studio 欧洲风格...
                             ￥39.95</Flex.Item>
                            <Flex.Item><img src='./images/02.png' />顺顺工艺欧洲风格塑料...
                             ￥83.99</Flex.Item>
                        </Flex>
                        <WhiteSpace size="lg" />
                        </div>
                    </div>
            </div>
        )
    }
}
