import React from 'react'
import {NavBar, Icon,TabBar } from 'antd-mobile';
import AppHome from './Home'
import Appidea from './idea'
import Shop from './shop'


export default class AppTab extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      selectedTab: 'home',
    };
  }
  render() {
    return (
      <div style={{ position: 'fixed', height: '100%', width: '100%', top: 0 }}>
        <TabBar
          unselectedTintColor="#949494"
          tintColor="#33cccc"
          barTintColor="white"
        >
          <TabBar.Item
            title="首页"
            key="Life"
            icon={<div style={{
              width: '22px',
              height: '22px',
              background: 'url(./images/home.png) center center /  21px 21px no-repeat' }}
            />
            }
            selectedIcon={
              <div style={{
                width: '22px',
                height: '22px',
                background: 'url(./images/home1.png) center center /  21px 21px no-repeat' }}
              />
            }
            selected={this.state.selectedTab === 'home'}
            onPress={() => {
              this.setState({
                selectedTab: 'home',
              });
            }}
          >
            <AppHome/>
          </TabBar.Item>
          <TabBar.Item
            icon={<div style={{
              width: '22px',
              height: '22px',
              background: 'url(./images/linggan.png) center center /  21px 21px no-repeat' }}
            />
            }
            selectedIcon={
              <div style={{
                width: '22px',
                height: '22px',
                background: 'url(./images/linggan1.png) center center /  21px 21px no-repeat' }}
              />
            }
            title="灵感"
            key="Koubei"
            selected={this.state.selectedTab === 'redTab'}
            onPress={() => {
              this.setState({
                selectedTab: 'redTab',
              });
            }}
          >
            <Appidea />
          </TabBar.Item>
          <TabBar.Item
            icon={<div style={{
              width: '22px',
              height: '22px',
              background: 'url(./images/shangcheng.png) center center /  21px 21px no-repeat' }}
            />
            }
            selectedIcon={
              <div style={{
                width: '22px',
                height: '22px',
                background: 'url(./images/shangcheng1.png) center center /  21px 21px no-repeat' }}
              />
            }
            title="商城"
            key="shop"
            dot
            selected={this.state.selectedTab === 'greenTab'}
            onPress={() => {
              this.setState({
                selectedTab: 'greenTab',
              });
            }}
          >
            <Shop />
          </TabBar.Item>
          <TabBar.Item
            icon={<div style={{
              width: '22px',
              height: '22px',
              background: 'url(./images/wode.png) center center /  21px 21px no-repeat' }}
            />
            }
            selectedIcon={
              <div style={{
                width: '22px',
                height: '22px',
                background: 'url(./images/wode1.png) center center /  21px 21px no-repeat' }}
              />}
            title="我的"
            key="my"
            selected={this.state.selectedTab === 'yellowTab'}
            onPress={() => {
              this.setState({
                selectedTab: 'yellowTab',
              });
            }}
          >
            <NavBar
                style={{background:'#33cccc',color:'white'}}

                onLeftClick={() => console.log('onLeftClick')}
                rightContent={[
                    <Icon key="0" type="search" style={{ marginRight: '16px' ,color:'white'}} />,
                    // <Icon key="1" type="ellipsis" />,
                ]}
                >
                    我的
            </NavBar>
          </TabBar.Item>
        </TabBar>
      </div>
    );
  }
}