import React, { Component } from 'react'
import './home.css'
import { NavBar, Icon,  Flex, WhiteSpace   } from 'antd-mobile';
import Banner from './homebanner';

export default class Home extends Component {
    render() {
        return (
            <div>
              <NavBar
                style={{background:'#33cccc',color:'white'}}

                onLeftClick={() => console.log('onLeftClick')}
                rightContent={[
                    <Icon key="0" type="search" style={{ marginRight: '16px' ,color:'white'}} />,
                    // <Icon key="1" type="ellipsis" />,
                ]}
                >
                    住吧家具
                </NavBar>
                <Banner />
                  
                <div className="flex-container">
                    <WhiteSpace size="lg" />
                    <Flex>
                        <Flex.Item><img src='./images/remen.png' /></Flex.Item>
                        <Flex.Item><img src='./images/siren.png' /></Flex.Item>
                        <Flex.Item><img src='./images/xuangou.png' /></Flex.Item>
                    </Flex>
                    <WhiteSpace size="lg" />
                </div>
                <div className="sub-title">
                    <img style={{marginLeft:-20,marginTop:-10}} src='./images/gang.png' />
                    热门推荐
                </div>
                <div className='datu'>
                 <img src='./images/remen1.png' />
                </div>
                
            </div>
        )
    }
}
